<template>
  <v-chart :options="chartOption" ref="myChart" :autoresize="true"></v-chart>
</template>

<script>
import ECharts from 'vue-echarts'
import {mapGetters, mapActions} from "vuex";
import 'echarts/lib/chart/line'

export default {
  name: "StockChart",
  components: {
    'v-chart': ECharts
  },
  computed: {
    ...mapGetters({
      stockPrice: 'getStockPrice',
      holdingPeriod: 'getHoldingPeriod',
      profit: 'getProfit',
      isChartLoading: 'getChartLoading',
      ti1: 'getTI1',
      ti2: 'getTI2',
      ti3: 'getTI3',
      ti4: 'getTI4',
    }),
    hp_update() {
      return this.holdingPeriod
    },
    chart_loading_state() {
      return this.isChartLoading
    }
  },
  methods: {
    ...mapActions([
      'actionChartLoading'
    ])
  },
  watch: {
    chart_loading_state: function () {
      if (this.isChartLoading)
        this.$refs.myChart.showLoading()
      else
        this.$refs.myChart.hideLoading()
    },
    hp_update: function () {
      const x = []
      for (let i = 0; i < this.stockPrice.length; i++) {
        x.push(i + 1)
      }
      this.chartOption.dataset.source = [
        x,
        this.stockPrice,
        this.holdingPeriod,
        this.ti1,
        this.ti2,
        this.ti3,
        this.ti4
      ]
      this.actionChartLoading(false)
    }
  },
  data() {
    return {
      chartOption: {}
    }
  },
  mounted() {
    this.$refs.myChart.showLoading()
    this.chartOption = {
      dataset: {
        source: []
      },
      xAxis:
          {
            type: 'category',
            boundaryGap:
                false
          }
      ,
      yAxis: {
        type: 'value',
        boundaryGap:
            ['1%', '1%'],
        scale: true,
      }
      ,
      series: [
        {
          type: 'line',
          smooth: 0,
          symbol: 'triangle',
          symbolSize: 8,
          lineStyle: {
            width: 1.5
          },
          seriesLayoutBy: 'row'
        },
        {
          type: 'line',
          smooth: 0,
          symbol: 'none',
          symbolSize: 10,
          lineStyle: {
            width: 4
          },
          seriesLayoutBy: 'row',
          areaStyle: {color: 'rgba(0,180,0,0.5)'}
        },
        {
          type: 'line',
          smooth: 0,
          symbol: 'none',
          symbolSize: 8,
          lineStyle: {
            width: 1
          },
          seriesLayoutBy: 'row',
        },
        {
          type: 'line',
          smooth: 0,
          symbol: 'none',
          symbolSize: 8,
          lineStyle: {
            width: 1
          },
          seriesLayoutBy: 'row',
        },
        {
          type: 'line',
          smooth: 0,
          symbol: 'none',
          symbolSize: 8,
          lineStyle: {
            width: 1
          },
          seriesLayoutBy: 'row',
        },
        {
          type: 'line',
          smooth: 0,
          symbol: 'none',
          symbolSize: 8,
          lineStyle: {
            width: 1
          },
          seriesLayoutBy: 'row',
        },
      ],
      visualMap: {
        type: 'piecewise',
        show: false,
        seriesIndex: 1,
      },
    }
    this.$refs.myChart.hideLoading()
  }
}
</script>

<style scoped>
.echarts {
  width: 100%;
  height: 400px;
}
</style>